@import '~antd/dist/antd.css';

@font-face {
  font-family: 'Roboto-Regular';
  src: url('util/fonts/Roboto-Regular.ttf');
}

@font-face {
  font-family: 'Candara';
  src: url('util/fonts/Candara.ttf');
}

body {
  background: rgba(0,0,0,.025);
}

.ant-divider-vertical {
  border-left: 1px solid #d3d3d3;
  color: #000;
  font-size: 24px;
}

.App {
  text-align: center;
}

.marginAuto {
  margin: auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.container {
  padding-top: 3%;
  padding-bottom: 3%;
}
@media (max-width: 500px) {
  .container {
    padding-top: 10%;
    padding-bottom: 10%;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo {
  position: relative;
  z-index: 100;
  background-image: url('./images/ppText.png');
  height: 3.5rem;
  width: 6rem;
  background-size: contain;
  background-position: left;
  background-repeat: no-repeat;
  margin-right: auto;
  margin-top: 1rem;
}
@media (max-width: 900px) {
  .logo {
    height: 2.5rem;
    width: 4.5rem;
    margin: 1rem;
  }
}
@media (max-width: 500px) {
}

.menuClass {
  border: none!important;
}

.menuClass .ant-menu-submenu-title {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
}

.menuIcon {
  font-size: 75%!important;
  padding-left: .5rem;
}

.headerClass {
  padding: 0;
  border-bottom: 1px solid #f0f0f0;
  box-shadow: 0 0.125rem 0.25rem rgba(0,0,0,.1);
}

.headerClass .ant-menu {
  padding: 0 2.5%;
  width: 100%;
  display: flex;
  width: 100%;
  align-items: center;
}

.headerClass .link-class {
  margin-right: auto;
}

.ant-layout-header, .headerClass .ant-menu, .ant-layout {
  background: transparent;
  border: none!important;
  box-shadow: none!important;
}

.ant-menu-item-active, .ant-menu-item-only-child, .ant-menu-item:hover, .ant-menu-submenu:hover {
  border: none!important;
}

.menuLeft {
  display: flex;
  align-items: center;
}

.mobileClass {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 75vh;
  width: 100%;
  overflow: scroll;
  z-index: 50;
  background-color: #f5f7fa;
  padding: 4rem 2rem;
  box-shadow: 0 0.125rem 1rem .5rem rgba(0,0,0,.5);
}
@media (min-width: 700px) {
  .mobileClass {
    height: 100vh;
  }
}

.mobileClass .ant-menu {
  text-align: center;
  flex-direction: column;
}

.mobileClass .ant-menu-submenu .ant-menu-item {
  text-align: left;
  width: 52.5%;
  margin-left: auto;
  padding-left: 0px!important;
}

.mobileClass .ant-menu-submenu {
  width: 100%;
}

.ant-menu-submenu-arrow {
  display: none;
}

.mobileClass .ant-menu-item, .mobileClass .ant-menu-submenu, .mobileClass button {
  margin-bottom: 1rem!important;
  font-family: 'Roboto';
  font-size: 115%;
}

.searchClass {
  top: 0;
  left: 0;
  right: 0;
  width: 100%!important;
  margin: auto!important;
}

.searchCont .ant-input-prefix {
  font-size: 175%!important;
  margin-right: 2rem;
}

.searchCont .ant-input-affix-wrapper, .ant-input-borderless {
  padding: 2.5% 10%;
  font-size: 175%!important;
}

.searchSugg {
  padding: 0 10% 5%;
}

.searchSugg h5 {
  padding: 1rem 0;
  border-top: 1px solid #f0f0f0;
  font-weight: bold!important;
}

.suggItem {
  padding: .5rem 1rem;
  background: rgba(0,0,0,.1);
  font-size: 125%;
  border-radius: 5px;
  margin-right: 1rem;
}

.ant-modal-close-x {
  font-size: 200%;
  color: black;
  margin: 1rem;
}

.homeClass {
  height: 125vh;
  background-color: white;
}
@media (max-width: 900px) {
  .homeClass {
    height: 148vh;
  }
}
@media (max-width: 500px) {
  .homeClass {
    height: 79vh;
  }
}

.homeTitle {
  font-weight: bold;
  margin-bottom: 0;
  font-size: 330%;
  padding: 0 1rem;
  letter-spacing: -1px;
  line-height: 4rem;
}
@media (max-width: 500px) {
  .homeTitle {
     font-size: 250%;
     line-height: 33px;
     padding-bottom: 10px;
   }
}

.titleSub {
  padding: 0 1rem;
  font-size: 200%;
  font-weight: 500;
}
@media (max-width: 500px) {
  .titleSub {
    padding: 1rem;
    font-size: 125%;
  }
}

.homeTop {
  margin: 5rem auto!important;
  margin-bottom: 0!important;
  display: flex;
  align-items: center;
}

.searchHome .ant-carousel {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.searchHome {
  position: absolute;
  top: 60%;
  height: 85vh;
  left: 0;
  right: 0;
}
@media (max-width: 900px) {
  .searchHome {
    top: 100%;
  }
}
@media (max-width: 500px) {
  .searchHome {
    height: 40vh;
    top: 60%;
  }  
}

.search {
  margin: auto;
  position: absolute;
  width: 100%;
  left: 0;
  right: 0;
  margin-top: -5rem;
  z-index: 10;
  box-shadow: 0 -0.5rem 0.5rem .25rem rgba(0,0,0,.033);
}

.search .ant-input-group-wrapper {
  border: 1.5rem solid #fff;
  background: #fff;
}

.search .ant-input-affix-wrapper {
  padding: 1rem;
  border: none;
}

.search .ant-input-affix-wrapper:focus, .search .ant-input-affix-wrapper:active, .search .ant-input-affix-wrapper:hover {
  box-shadow: none;
}

.search button {
  padding: 1rem 2rem!important;
  height: auto!important;
  font-weight: bold;
}

.contentStyle {
  height: 85vh;
  color: #fff;
  line-height: 160px;
  text-align: center;
  background: #364d79;
  object-fit: contain;
}
@media (max-width: 500px) {
  .contentStyle {
    height: 40vh;
  }  
}

.homeTop .slick-list {
  border-radius: 10px;
}

.carouselClass {
  height: -webkit-fill-available;
  width: -webkit-fill-available;
  object-fit: cover;
}

.viewCard {
  margin-top: 12.5%;
  overflow: hidden;
}

.ant-card {
  border-radius: 7.5px;
  overflow: hidden;
}
@media (max-width: 500px) {
  .viewCard .ant-card .ant-card-cover {
    display: none;
  }
}
@media (min-width: 500px) {
  .viewCard:before {
    content: '';
    position: absolute;
    padding-right: 5rem;
    height: 100%;
    width: 100%;
    background-image: url('./images/home/ppBG.png');
    background-position: top right;
    background-size: 20%;
    background-repeat: no-repeat;
  }
}

.catCard {
  height: 100%;
  padding: 1rem;
  border-radius: 0!important;
}

.catCard svg {
  font-size: 1.75rem;
}
@media (max-width: 500px) {
  .catCard {
    padding: .5rem;
    height: 175px;
  }
  .catCard svg {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
  }
}

.linkRow {
  margin: 2rem auto!important;
  width: 75%;
}
@media (max-width: 500px) {
  .linkRow {
    width: 100%;
  }
}

.cardLink {
  text-align: center;
}

.linkClass {
  color: black;
  background-color: rgba(0,0,0,.1);
  padding: 1.75rem 0!important;
}

.linkClass:hover {
  background-color: #1890ff;
  transition: .5s;
  color: white;
}

.linkCard .ant-card-body {
  display: flex;
}

.cardBody {
  padding-top: 1rem;
}

.viewCard .ant-card-meta-title {
  color: #808080;
  font-size: .75rem;
}


.cardImg {
  margin-left: 1.5rem;
  height: 4.5rem;
  width: 4.5rem;
  object-fit: contain;
}

.subscribe {
  background-color: #1f2430;
  margin: 5rem auto 0;
  padding: 5% 10%;
  text-align: center;
}

.subscribe h1 {
  color: white;
  font-weight: bold;
  width: 66%;
  margin: auto;
}
@media (max-width: 900px) {
  .subscribe h1 {
    width: 95%;
    font-size: 125%;
  }
}

.subButton {
  background: #e97284;
  border: none;
  font-size: 120%;
  height: auto;
  margin: 2rem 0 1rem;
  border-radius: 5px;
  padding: .75rem 1.75rem;
  box-shadow: 0 0.5rem 0.5rem rgba(0,0,0,.25);
}
.subButton:hover, .subButton:active, .subButton:focus {
  background: #bb384c
}

.newsletter {
  margin-top: 10%;
  margin-bottom: 10%;
  padding: 5% 0;
  border-radius: 15px;
  box-shadow: 0 0.5rem 0.5rem rgba(0,0,0,.05);
  background-color: white;
  background-image: url('./images/newsletter.png');
  background-position: -75% 50%;
  background-size: 75%;
  background-repeat: no-repeat;
}
.newsletter .ant-col {
  display: flex;
  align-items: center;
  width: 100%;
}
.newsletter button {
  margin-left: auto;
  margin-right: auto;
  border-radius: 5px;
  padding: .25rem 1rem;
  height: 40px;
}
@media (max-width: 500px) {
  .newsletter {
    background-position: -25% 50%;
    background-size: cover;    
  }

  .newsletter .ant-col {
    padding: 2rem .75rem;
    flex-direction: column;
    align-items: flex-start;
  }

  .newsletter button {
    margin-left: 0;
    margin-top: 1rem;
  }
}

.footerClass {
  background: white;
  padding: 4rem 0;
  font-family: 'Roboto-Regular';
}

.footerClass li a {
  color: black!important;
  font-size: 110%;
  line-height: 2.5rem;
}

.footerClass li a:hover {
  color: #1890ff!important;
}

@media (max-width: 900px) {
  .footerClass li a {
    font-size: 100%;
  }
  .footerClass {
    text-align: center;
  }
  .logoFooter {
    margin: auto auto 2rem auto;
  }
}

.footerBottom {
  display: flex;
  width: 100%;
  margin: 2rem 0 1rem;
}

.footerIcons svg {
  font-size: 2rem;
  margin-right: 2rem;
}

.footerBtn {
  margin-left: auto;
  display: flex;
  align-items: center;
}

@media (max-width: 900px) {
  .footerBottom {
    flex-direction: column;
  }  
  .footerIcons svg {
    margin: auto 1rem;
  }
  .footerBtn {
    margin: 2rem auto;
    flex-direction: column-reverse;
  }
}


/*-- Categroies --*/

.catIcon {
  font-size: 3rem;
  padding-top: 2rem;
}

.catCont {
  border-radius: 50%;
  height: 175px;
  width: 174px;
}
@media (max-width: 900px) {
  .catIcon {
    font-size: 2rem;
    padding-top: 1rem;
  }
  .catTitle {
    font-size: 100%!important;
  }
  .catCont {
    height: 125px;
    width: 125px;
  }
}


/*-- How To --*/

.howClass {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 8rem;
}

@media (min-width: 900px) {
  .howClass:before {
    content: '';
    position: absolute;
    height: 45%;
    width: 100%;
    margin-top: 5rem;
    background-position: right;
    background-repeat: no-repeat;
    background-image: url('./images/subscribe/doodles.png');
    transform: scaleX(-1);
    background-size: contain;
  }
  .howClass:after {
    content: '';
    position: absolute;
    height: 45%;
    width: 100%;
    margin-top: 7rem;
    background-position: right;
    background-repeat: no-repeat;
    background-image: url('./images/subscribe/doodles.png');
    background-size: contain;
  }
}

.howTitle {
  font-size: 150%;
  width: 50%;
  margin-top: 1rem;
  text-align: center;
}

.howHeader {
  text-align: center;
  font-weight: bold;
  margin-top: 1rem;
}

.howImg {
  width: 100%;
  height: auto;
}
@media (max-width: 900px) {
  .howTitle {
    font-size: 125%;
    width: 80%;
  }
  .howHeader {
    font-weight: bold!important;
    font-size: 240%!important; 
  }
}
@media (min-width: 500px) and (max-width: 900px) {
  .howImg {
    width: 75%;
  }
}

.howlogo {
  width: 50%;
  height: auto;
}
@media (max-width: 900px) {
  .howlogo {
    display: none;
  }
}

.plans {
  margin-top: 15vh;
  background-color: #005ff9;
  text-align: center;
  padding: 2rem 0;
}

.planCont {
  background-color: white;
  height: 525px;
  margin-top: 2rem;
  padding: 2rem;
}

.planTitle {
  color: white!important;
  text-align: center;
}
@media (max-width: 900px) {
  .planTitle {
    font-size: 180%!important;
  } 
}

.features {
  margin: 2rem auto;
}

.iconSub {
  font-size: 1.5rem;
  margin: .25rem .5rem;
}

.cardSub {
  padding: 1rem 2rem;
}
@media (max-width: 900px) {
  .cardSub h3 {
    font-size: 135%;
  }
}


/*-- About --*/

.nameCont {
  z-index: 1;
  position: relative;
  margin: auto;
  margin-top: 15vh;
  width: 85%;
  height: 66.6vh;
  background-color: #e6f7ff;
  background-image: url('./images/about/about.svg');
  background-position: center;
  background-size: 90%;
  background-repeat: no-repeat;
  padding: 3rem;
}

.nameCont p {
  font-size: 150%;
}

.aboutImg {
  margin: 3rem 0 2rem;
  height: auto;
  width: 75%;
}

.aboutValues {
  margin: 1rem auto 3rem;
}
@media (max-width: 900px) {
  .nameCont {
    margin-top: 25vh;
    height: 45vh;
    background-image: none;
  }
  .nameCont p {
    width: 100%;
  }
  .aboutImg {
    width: 90%;
  }
  .aboutRow {
    justify-content: center!important;
  }
  .aboutValues {
    text-align: center;
  }
  .aboutValues span {
    text-align: center!important;
  }
 .aboutValues p {
    font-size: 110%!important;
  }
}
@media (max-width: 500px) {
  .nameCont {
    height: 30vh;
    background-image: none;
  }
  .nameCont p {
    font-size: 125%;
  }
}

.mapImg {
  height: auto;
  background-image: url('./images/about/map1.png');
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: white;
  position: relative;
  height: 100%;
  width: 50%;
  margin: auto;
}
@media (max-width: 1200px) {
  .mapImg {
    width: 75%;
  }
}
@media (max-width: 800px) {
  .mapImg {
    width: 60%;
  }
}
@media (max-width: 500px) {
  .mapImg {
    width: 100%;
  }
}

.aniDot {
  width: 1.25rem;
  height: 1.25rem;
  border: 0;
  border-radius: 50%;
  z-index: 10;
  background-color: white;
  position: absolute;

}

.smDot {
  outline: none;
  width: 1.25rem;
  height: 1.25rem;
  border: none;
  border-radius: 50%;
  background-color: white;
  position: absolute;
  top: 0%;
  left: 0%;
  opacity: 0;
  animation-name: dotAnimation;
  animation-iteration-count: infinite;
  animation-duration: 3s;
}

.northW {
  top: 25%;
  left: 35%;
}
.northE {
  top: 37.5%;
  left: 72.5%;
}
.northC {
  top: 52.5%;
  left: 37.5%;
}
.southW {
  top: 62.5%;
  left: 15%;
}
.southE {
  top: 72.5%;
  left: 40%;
}
.southS {
  top: 77.5%;
  left: 30%;
}
@media (max-width: 1400px) {
  .northW {
    top: 32.5%;
  }
  .southW {
    top: 58%;
  }
  .southE {
    top: 65.5%;
  }
  .southS {
    top: 70.5%;
  }
}
@media (max-width: 1200px) {
  .northW {
    top: 30%;
  }
  .southW {
    top: 60%;
  }
  .southE {
    top: 70%;
  }
  .southS {
    top: 75%;
  }
}
@media (max-height: 1400px) and (max-width: 1100px) {
 .northW {
    top: 37.5%;
  }
 .northE {
    top: 45%;
  }
  .southW {
    top: 55%;
  }
  .southE {
    top: 60%;
  }
  .southS {
    top: 62.5%;
  }
}
@media (max-width: 850px) {
  .aniDot{
    width: .75rem;
    height: .75rem;
  }
  .smDot{
    width: .75rem;
    height: .75rem;
  }
  .northW {
    top: 20%;
  }
  .northE {
    left: 70%;
    top: 35.5%;
  }
  .southW {
    left: 20%;
    top: 62.5%;
  }
  .southE {
    left: 41.5%;
    top: 80%;
  }
  .southS {
    left: 32.5%;
    top: 85%;
  }
}
@media (max-width: 800px) {
  .northW {
    top: 25%;
    left: 35%;
  }
  .northE {
    top: 37.5%;
    left: 72.5%;
  }
  .northC {
    top: 52.5%;
    left: 37.5%;
  }
  .southW {
    top: 62.5%;
    left: 15%;
  }
  .southE {
    top: 72.5%;
    left: 40%;
  }
  .southS {
    top: 77.5%;
    left: 30%;
  }
}
@media (max-width: 500px) {
  .northW {
    top: 37.5%;
  }
  .northE {
    top: 45%;
  }
  .southW {
    top: 56%;
  }
  .southE {
    top: 61.5%;
  }
  .southS {
    top: 63.5%;
  }
}

@keyframes dotAnimation {
  0%   {opacity: 0.25;}
  50%  {opacity: .75;}
  90%  {opacity: 0; transform: scale(2);}
}

.stateClass {
  background-color: #f0f8ff;
  padding: 1rem;
  border-radius: 10px;
}

.stateClass ul {
  padding: 0;
  list-style: none;
}